<template>
  <div id="contactus" class="container">
    <h1 class="h1"></h1>
    <p class="text-center mb-10">
      
      <br />
      
      <br />
      
    </p>

    <v-row class="contactrow">
      <v-col cols="12" lg="6">
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3616.184212395652!2d121.50258431529059!3d24.99385574605288!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x34680277189b1b6b%3A0x569614ee54a47641!2z5o236YGL5pmv5a6J56uZ!5e0!3m2!1szh-TW!2stw!4v1644739185381!5m2!1szh-TW!2stw"
          width="100%"
          height="100%"
          style="border: 0"
          allowfullscreen=""
          loading="lazy"
        ></iframe>
      </v-col>
      <v-col cols="12" lg="6">
        <v-form>
          <v-row>
            <v-col cols="6">
              <v-text-field
                solo
                label=""
                clearable
                :rules="rules.name"
                v-model="name"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                solo
                label=""
                clearable
                :rules="rules.title"
                v-model="title"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                solo
                label=""
                clearable
                :rules="rules.email"
                v-model="email"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                solo
                label=""
                clearable
                :rules="rules.phone"
                v-model="phone"
              ></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-textarea
                solo
                name="input-7-4"
                label=""
                :rules="rules.text"
                v-model="text"
              ></v-textarea>
            </v-col>
            <v-btn block color="#00ACC1" class="white--text" @click="send()">
              
            </v-btn>
          </v-row>
        </v-form>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name: '',
        title: '',
        email: '',
        phone: '',
        text: ''
      }
    },
    computed: {
      rules() {
        return {
          name: [v => !!v || ''],
          title: [v => !!v || ''],
          email: [v => !!v || ''],
          phone: [v => !!v || ''],
          text: [v => !!v || '']
        }
      }
    },
    methods: {
      send() {
        if (
          !this.name ||
          !this.title ||
          !this.email ||
          !this.phone ||
          !this.text
        ) {
          this.$swal({
            icon: 'error',
            title: '',
            text: ''
          })
        } else {
          this.$swal({
            icon: 'success',
            title: '',
            text: ''
          })
        }
      }
    }
  }
</script>
