<template>
  <div id="cart" class="container">
    <v-stepper v-model="e1">
      <!--  -->
      <v-stepper-header style="width: 80%" class="mx-auto">
        <v-stepper-step :complete="e1 > 1" step="1" color="#00ACC1">
          
        </v-stepper-step>

        <v-divider color="#00ACC1"></v-divider>

        <v-stepper-step :complete="e1 > 2" step="2" color="#00ACC1">
          
        </v-stepper-step>

        <v-divider color="#00ACC1"></v-divider>

        <v-stepper-step step="3" color="#00ACC1"></v-stepper-step>
      </v-stepper-header>

      <!--  -->
      <v-stepper-items>
        <!--  -->
        <v-stepper-content step="1">
          <div class="text-center">
            <v-data-table :headers="headers" :items="products">
              <template v-slot:item.image="{ item }">
                <!-- {{ item }} -->
                <img
                  v-if="item.product.image"
                  :src="item.product.image"
                  style="height: 100px"
                />
              </template>

              <template v-slot:item.name="{ item }">
                {{ item.product.name }}
              </template>

              <template v-slot:item.price="{ item }">
                $
                {{ new Intl.NumberFormat('en-IN').format(item.product.price) }}
              </template>

              <template v-slot:item.action="{ item, index }">
                <!-- {{index}} -->
                <div class="d-flex align-center">
                  <v-text-field
                    label=""
                    type="number"
                    style="width: 10px"
                    v-model="item.quantity"
                    @input="updateCart(index, item.quantity)"
                  ></v-text-field>
                  <v-btn text color="red" @click="updateCart(index, 0)">
                    <v-icon large>mdi-trash-can-outline</v-icon>
                  </v-btn>
                </div>
              </template>
            </v-data-table>
            <v-divider></v-divider>
            <h2 class="mb-15 mt-5 text-right">
              {{ new Intl.NumberFormat('en-IN').format(total) }} 
            </h2>

            <v-btn class="cart-btn" to="/products" text></v-btn>

            <v-btn class="ml-10 cart-btn" @click="e1 = 2" text></v-btn>
          </div>
        </v-stepper-content>

        <!--  -->

        <v-stepper-content step="2">
          <h1 class="text-center mb-15 mt-10 h1"></h1>
          <v-form ref="form" lazy-validation @submit.prevent="submitorders">
            <v-row>
              <v-col cols="12" md="6">
                <v-label for="name"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="name"
                  v-model="form.name"
                  :rules="rules.nameRules"
                  placeholder=""
                  required
                ></v-text-field>
              </v-col>
              <v-col>
                <v-label for="contact"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="contact"
                  v-model="form.phone"
                  :rules="rules.phoneRules"
                  placeholder=""
                  required
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="12" md="6">
                <v-label for="email"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="email"
                  v-model="form.email"
                  :rules="rules.emailRules"
                  placeholder=""
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-label for="address">()</v-label>
                <v-text-field
                  solo
                  clearable
                  id="address"
                  v-model="form.address"
                  placeholder=""
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="6">
                <v-radio-group v-model="form.pickupway" row class="mr-15">
                  <v-label></v-label>
                  <v-radio label="" value=""></v-radio>
                  <v-radio label="" value=""></v-radio>
                </v-radio-group>
              </v-col>
              <v-col cols="6">
                <v-radio-group v-model="form.pay" row>
                  <v-label></v-label>
                  <v-radio label="" value=""></v-radio>
                  <v-radio label="ATM" value="ATM"></v-radio>
                </v-radio-group>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-label for="memo"></v-label>
                <v-textarea
                  solo
                  clearable
                  outlined
                  id="memo"
                  v-model="form.memo"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-form>
          <div class="text-center">
            <v-btn class="cart-btn" @click="e1 = 1" text></v-btn>

            <v-btn class="cart-btn ml-10" @click="e1 = 3" type="submit" text>
              
            </v-btn>
          </div>
        </v-stepper-content>

        <!--  -->
        <v-stepper-content step="3" class="step3">
          <div class="text-center">
            <h1 class="text-center mb-15 mt-10"></h1>
            <v-simple-table class="mb-15 text-center">
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-center"></th>
                    <th class="text-center"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td></td>
                    <td>{{ form.name }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ form.email }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ form.address }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ form.phone }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ form.pickupway }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ form.pay }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ form.memo }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>

            <v-btn class="cart-btn" @click="e1 = 2" text></v-btn>

            <v-btn class="cart-btn ml-5" @click="e1 = 1" text></v-btn>

            <v-btn class="cart-btn ml-5" text @click="checkout"></v-btn>
          </div>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        e1: 1,
        products: [],
        headers: [
          { text: '', value: 'image' },
          { text: '', value: 'name' },
          { text: '', value: 'price' },
          { text: '', value: 'action' }
        ],
        form: {
          name: '',
          email: '',
          address: '',
          phone: '',
          memo: '',
          pay: '',
          pickupway: ''
        }
      }
    },
    computed: {
      rules() {
        return {
          nameRules: [v => !!v || ''],
          emailRules: [
            v => !!v || '',
            v => /.+@.+\..+/.test(v) || ''
          ],
          phoneRules: [v => !!v || '']
        }
      },
      total() {
        return this.products.reduce((accumulator, currentValue) => {
          return (
            accumulator + currentValue.quantity * currentValue.product.price
          )
        }, 0)
      }
    },
    methods: {
      async updateCart(index, quantity) {
        try {
          // 
          await this.api.patch(
            'users/me/cart',
            {
              product: this.products[index].product._id,
              quantity
            },
            {
              headers: {
                authorization: 'Bearer ' + this.user.token
              }
            }
          )
          // 
          if (quantity === 0) {
            this.products.splice(index, 1)
            this.$store.commit('user/updateCart', this.user.cart - 1)
          }
        } catch (error) {
          this.$swal({
            icon: 'error',
            title: '',
            text: ''
          })
        }
      },
      async checkout() {
        try {
          await this.api.post('/orders', this.form, {
            headers: {
              authorization: 'Bearer ' + this.user.token
            }
          })
          this.$swal({
            icon: 'success',
            title: '',
            text: '!'
          })
          this.$router.push('/back/member/orders')
        } catch (error) {
          this.$swal({
            icon: 'error',
            title: '',
            text: ''
          })
        }
      }
    },
    async created() {
      // vuex
      this.form.name = this.user.name
      this.form.email = this.user.email
      this.form.address = this.user.address
      this.form.phone = this.user.phone
      try {
        const { data } = await this.api.get('users/me/cart', {
          headers: {
            authorization: 'Bearer ' + this.user.token
          }
        })
        this.products = data.result
      } catch (error) {
        this.$swal({
          icon: 'error',
          title: '',
          text: ''
        })
      }
    }
  }
</script>
