<template>
  <div id="appointmentCheckout">
    <v-stepper v-model="e1">
      <!--  -->
      <v-stepper-header style="width: 80%" class="mx-auto">
        <v-stepper-step :complete="e1 > 1" step="1" color="#00ACC1">
          
        </v-stepper-step>

        <v-divider color="#00ACC1"></v-divider>

        <v-stepper-step :complete="e1 > 2" step="2" color="#00ACC1">
          
        </v-stepper-step>
      </v-stepper-header>

      <!-- -->
      <v-stepper-items>
        <!--  -->
        <v-stepper-content step="1">
          <h1 class="text-center mb-15 mt-10 h1"></h1>
          <v-form ref="form" lazy-validation>
            <v-row>
              <v-col cols="6">
                <v-label for="name"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="name"
                  v-model="form.name"
                  :rules="rules.nameRules"
                  placeholder=""
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-label for="contact"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="contact"
                  v-model="form.phone"
                  :rules="rules.phoneRules"
                  placeholder=""
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-label for="email"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="email"
                  v-model="form.email"
                  :rules="rules.emailRules"
                  placeholder=""
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="6">
                <v-label for="petname"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="petname"
                  v-model="form.petname"
                  placeholder=""
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-label for="petbreed"></v-label>
                <v-text-field
                  solo
                  clearable
                  id="petbreed"
                  v-model="form.petbreed"
                  placeholder=""
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-label for="memo"></v-label>
                <v-textarea solo id="memo" v-model="form.memo"></v-textarea>
              </v-col>
            </v-row>
          </v-form>

          <!--  -->
          <div class="text-center mt-10">
            <v-btn class="checkout-btn" to="/appointment" text></v-btn>
            <v-btn class="checkout-btn ml-10" @click="e1 = 2" text>
              
            </v-btn>
          </div>
        </v-stepper-content>

        <!--  -->
        <v-stepper-content step="2" class="confirmAppointment">
          <h1 class="text-center mb-15 mt-10 h1"></h1>
          <v-simple-table class="mb-15 text-center">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center"></th>
                  <th class="text-center"></th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td></td>
                  <td>{{ form.appointmentdate }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.appointmenttime }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.serviceitem }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.pettype }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.name }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.phone }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.email }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.petname }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.petbreed }}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{{ form.memo }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <div class="text-center">
            <v-btn class="checkout-btn" @click="e1 = 1" text></v-btn>
            <v-btn class="checkout-btn ml-5" text @click="appointmentCheckOut">
              
            </v-btn>
          </div>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        fromDateMenu: false,
        e1: 1,
        headers: [
          { text: '', value: 'image' },
          { text: '', value: 'name' },
          { text: '', value: 'price' },
          { text: '', value: 'action' }
        ],
        form: {
          name: '',
          phone: '',
          email: '',
          petname: '',
          petbreed: '',
          appointmentdate: null,
          memo: '',
          serviceitem: '',
          pettype: '',
          appointmenttime: null
        }
      }
    },
    computed: {
      rules() {
        return {
          nameRules: [v => !!v || ''],
          emailRules: [
            v => !!v || '',
            v => /.+@.+\..+/.test(v) || ''
          ],
          phoneRules: [v => !!v || '']
        }
      }
    },
    methods: {
      async appointmentCheckOut() {
        try {
          console.log(this.form)
          await this.api.post('/appointments', this.form, {
            headers: {
              authorization: 'Bearer ' + this.user.token
            }
          })
          this.$swal({
            icon: 'success',
            title: '',
            text: '!'
          })
          this.$router.push('/back/member/appointment')
        } catch (error) {
          this.$swal({
            icon: 'error',
            title: '',
            text: ''
          })
        }
      }
    },
    async created() {
      // vuex
      this.form.name = this.user.name
      this.form.email = this.user.email
      this.form.address = this.user.address
      this.form.phone = this.user.phone
      this.form.pettype = this.user.appointment.pettype
      this.form.serviceitem = this.user.appointment.serviceitem
      this.form.appointmenttime = this.user.appointment.time
      this.form.appointmentdate = this.user.appointment.appointmentDate
      // console.log(this.form)
    }
  }
</script>
