<template>
  <div id="register">
    <h1 class="text-center"> </h1>
    <div class="box">
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
        @submit.prevent="register"
      >
        <label for="account"> :</label>
        <v-text-field
          id="account"
          v-model="form.account"
          :counter="10"
          :rules="rules.accountRules"
          placeholder=""
          required
        ></v-text-field>

        <label for="password"> :</label>
        <v-text-field
          id="password"
          v-model="form.password"
          :type="passwordshow ? 'text' : 'password'"
          :rules="rules.passwordRules"
          :append-icon="passwordshow ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="passwordshow = !passwordshow"
          placeholder=""
          required
        ></v-text-field>

        <label for="password"> :</label>
        <v-text-field
          id="password"
          :type="passwordshow ? 'text' : 'password'"
          :rules="rules.passwordCheckRules"
          :append-icon="passwordshow ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="passwordshow = !passwordshow"
          placeholder=""
          required
        ></v-text-field>

        <div class="btn-wrap">
          <v-btn
            :disabled="!valid"
            color="orange darken-1 white--text"
            class="mr-4 mt-10"
            type="submit"
          >
            
          </v-btn>
        </div>
      </v-form>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        passwordshow: false,
        valid: true,
        form: {
          account: '',
          password: ''
        }
      }
    },
    computed: {
      rules() {
        return {
          accountRules: [
            v => !!v || '',
            v =>
              (v && v.length <= 10 && v.length >= 4) ||
              '4-10'
          ],
          passwordRules: [
            v => !!v || '',
            v => (v && v.length >= 4) || '4'
          ],
          passwordCheckRules: [
            v => !!v || '',
            v => (v && v === this.form.password) || ''
          ]
        }
      }
    },

    methods: {
      async register() {
        try {
          await this.api.post('/users', this.form)
          this.$swal({
            icon: 'success',
            title: '',
            text: ''
          })
          this.$router.push('/')
        } catch (error) {
          this.$swal({
            icon: 'error',
            title: '',
            text: error.response.data.message
          })
        }
      }
    }
  }
</script>
